import React, { useCallback, useMemo } from 'react';
import {
  Box, Button, FormControl, FormLabel, Heading, Input, useStyleConfig
} from '@chakra-ui/react';
import { useTranslation } from 'react-i18next';
import { shallowEqual } from 'react-redux';
import MiniBox from '../../../../layout/MiniBox';
import AddressSearch from '../../../../components/AddressSearch';
import { useAppAsyncDispatch, useAppSelector } from '../../../../hooks/store';
import { apiErrorHandler } from '../../../../utils/general';
import { cardPayload } from '../../../../types/card';
import { updateCardPayload } from '../../../../store/actionCreators/card';
import { cardDelete, createCard } from '../../../../store/middlewares/card';
import { useDrawer } from '../../../../hooks/drawer';

const CardCreated = React.lazy(() => import('../CardCreated/CardCreated'));

const CardBillingAddress = () => {
  const { t } = useTranslation();
  const dispatch = useAppAsyncDispatch();
  const { open, goBack } = useDrawer();

  const style:any = useStyleConfig('FormControl', {
    variant: 'primary'
  });

  const cardPayloadData = useAppSelector((store) => store.card.cardPayload, shallowEqual);
  const replaceCardId = useAppSelector((store) => store.card.replaceCardId);

  const setPayload = useCallback((key: keyof cardPayload, value: any) => {
    if (cardPayloadData) {
      const copiedData:cardPayload = {
        ...cardPayloadData,
        [key]: value
      };

      dispatch(updateCardPayload(copiedData));
    }
  }, [cardPayloadData]);

  const isValid = useMemo(() => (
    cardPayloadData
      && !!cardPayloadData.shipping
      && !!cardPayloadData.shipping.shippingAddress.line1
      && !!cardPayloadData.shipping.shippingAddress.city
      && !!cardPayloadData.shipping.shippingAddress.state
      && !!cardPayloadData.shipping.shippingAddress.country
      && !!cardPayloadData.shipping.shippingAddress.postalCode
      && cardPayloadData.shipping.shippingAddress.line1.length !== 0
      && cardPayloadData.shipping.shippingAddress.city.length !== 0
      && cardPayloadData.shipping.shippingAddress.state.length !== 0
      && cardPayloadData.shipping.shippingAddress.country.length !== 0
      && cardPayloadData.shipping.shippingAddress.postalCode.length !== 0
  ), [cardPayloadData]);

  const submit = useCallback(() => {
    if (isValid && cardPayloadData) {
      dispatch(createCard(cardPayloadData))
        .then(() => {
          if (replaceCardId) {
            dispatch(cardDelete(replaceCardId))
              .catch(apiErrorHandler)
              .finally(() => {
                open(CardCreated);
              });
          } else {
            open(CardCreated);
          }
        }).catch(apiErrorHandler);
    }
  }, [isValid, replaceCardId]);

  return (
    <div className="page">
      <form onSubmit={(e) => {
        e.preventDefault();
        submit();
      }}
      >
        <MiniBox
          heading={t(`create_card_info.title_${cardPayloadData?.cardType}`)}
          onBack={goBack}
          footer={(
            <Button width="100%" variant="primary" disabled={!isValid} onClick={submit}>
              {t('create_card_address.ctaText')}
            </Button>
)}
        >
          <Box width="100%">

            <Heading variant="primary" mt={3} size="md">
              {t('create_card_address.confirmBillingAddressTitle')}
            </Heading>
            <FormControl variant="primary" style={style}>
              <FormLabel variant="primary">
                {t('create_card_address.address1')}
              </FormLabel>
              <AddressSearch
                placeholder={t('create_card_address.address1')}
                googleApiKey={process.env.REACT_APP_GMAP_KEY || ''}
                address={cardPayloadData?.shipping?.shippingAddress || undefined}
                onChange={(address) => {
                  setPayload('shipping', {
                    shippingAddress: {
                      ...address,
                      addressType: 'mailing',
                    }
                  });
                }}
              />
              {/* <Input variant="primary" value="" /> */}
            </FormControl>

            <FormControl variant="primary" style={style}>
              <FormLabel variant="primary">
                {t('create_card_address.address2')}
              </FormLabel>
              <Input
                variant="primary"
                placeholder={t('create_card_address.address2')}
                value={cardPayloadData?.shipping?.shippingAddress?.line2}
                onChange={(e: React.ChangeEvent<HTMLInputElement>) => {
                  setPayload('shipping', {
                    shippingAddress: {
                      ...cardPayloadData?.shipping?.shippingAddress || {},
                      line2: e.target.value
                    }
                  });
                }}
              />
            </FormControl>

            <FormControl variant="primary" style={style}>
              <FormLabel variant="primary">
                {t('create_card_address.addressCity')}
              </FormLabel>
              <Input
                variant="primary"
                placeholder={t('create_card_address.addressCity')}
                value={cardPayloadData?.shipping?.shippingAddress?.city}
                onChange={(e: React.ChangeEvent<HTMLInputElement>) => {
                  setPayload('shipping', {
                    shippingAddress: {
                      ...cardPayloadData?.shipping?.shippingAddress || {},
                      city: e.target.value
                    }
                  });
                }}
              />
            </FormControl>

            <FormControl variant="primary" style={style}>
              <FormLabel variant="primary">
                {t('create_card_address.addressState')}
              </FormLabel>
              <Input
                variant="primary"
                placeholder={t('create_card_address.addressState')}
                value={cardPayloadData?.shipping?.shippingAddress?.state}
                onChange={(e: React.ChangeEvent<HTMLInputElement>) => {
                  setPayload('shipping', {
                    shippingAddress: {
                      ...cardPayloadData?.shipping?.shippingAddress || {},
                      state: e.target.value
                    }
                  });
                }}
              />
            </FormControl>

            <FormControl variant="primary" style={style}>
              <FormLabel variant="primary">
                {t('create_card_address.addressZipcode')}
              </FormLabel>
              <Input
                variant="primary"
                placeholder={t('create_card_address.addressZipcode')}
                value={cardPayloadData?.shipping?.shippingAddress?.postalCode}
                onChange={(e: React.ChangeEvent<HTMLInputElement>) => {
                  setPayload('shipping', {
                    shippingAddress: {
                      ...cardPayloadData?.shipping?.shippingAddress || {},
                      postalCode: e.target.value
                    }
                  });
                }}
              />
            </FormControl>

          </Box>
        </MiniBox>
      </form>
    </div>
  );
};

export default CardBillingAddress;
